function Policies() {
    return (
        <main className="row">
            <section className="col-12">
                <h1 className="my-3 my-lg-5">Políticas de devolución y retiro de compras</h1>
                <div className="mx-4">
                    <h5 className="mt-2">Al realizar la compra, podrás seleccionar la opción de <b>retirar</b> la misma <b>por nuestro local</b>. Te llegará un mail o Whatsapp cuando tu pedido este listo, lo cual puede demorar hasta 5 días hábiles (sin contar sábados, domingos ni feriados) desde que realizaste la compra. Si necesitás retirar antes de ese plazo, consultanos por nuestro stock de entrega inmediata (el stock del local), ya que el stock de ventas online es otro.</h5>
                    <h5 className="mt-2">Cuando te acerques a retirar tu pedido, vamos a solicitarte tu número de orden de compra y también a nombre de quién figura la misma, para poder localizarla.</h5>  
                    <h5 className="mt-2"><b>Los pedidos serán guardados 90 días corridos (incluyendo sábados, domingos y feriados) desde que se realizó la compra. Luego de este plazo, no se hará entrega del pedido ni devolución del dinero abonado. Mercadopago no realiza devoluciones luego de los 90 días.</b></h5>
                    <h5 className="mt-2">El cliente tiene 30 días corridos para realizar la devolución desde que efectuó la compra. Para realizar la devolución, se tiene en cuenta el valor que se pagó en el momento de la compra. Si adquiriste un producto en descuento, se tomará el valor abonado en ese momento.</h5>
                    <h5 className="mt-2"><b>¿Cómo realizo una devolución?</b></h5>
                    <h5 className="mt-2">Deberás enviarnos tu compra en perfectas condiciones y en su packaging original. Una vez recibida, procederemos al cambio. Los costos de envío corren por cuenta del cliente. También podés acercarte a nuestro local, con tu número de orden de compra. Si necesitás ayuda o asesoramiento, contactanos!</h5>
                    <h5 className="mt-2"><b>Devoluciones por fallas</b></h5>
                    <h5 className="mt-2">En caso de que tu producto haya llegado con alguna falla, nos haremos cargo de los costos de envío para realizar la devolución. Te pedimos que nos contactes de manera inmediata para poder brindarte una solución.</h5>
                </div>
            </section>
        </main>
    )
}

export default Policies;